.title{margin:0;margin-bottom:7px;font-size:14;color:var(--black);font-weight:400;width:100%}.input{max-width:100%;background-color:var(--white);border:0.0625rem solid #dfeaf2;border-radius:15px;height:2.5rem;display:flex;align-items:center;padding-inline:0.9375rem;min-width:11.25rem;transition:border var(--transition)}.input>input{width:97%;color:var(--text-color);outline:none;border:none;background-color:rgba(0,0,0,0);font-size:14}.input>input::placeholder{color:var(--text-color)}.input>.avatar{width:100%;display:flex;justify-content:space-between;column-gap:1rem;align-items:center}.input>.avatar>input{display:none}.input>.avatar>p{color:var(--text-color);margin:0;white-space:nowrap}.input>.avatar :last-child{font-size:0.75rem;max-width:50%;overflow:hidden;text-overflow:ellipsis}.input:has(input:focus){border-color:var(--text-color)}.input:has(input:focus)>input::placeholder{color:var(--text-color) !important}.selector{background-color:var(--white);border:0.0625rem solid #dfeaf2;border-radius:clamp(0.75rem,0.6839788732rem + 0.2816901408vw,0.9375rem);height:clamp(1.875rem,1.6549295775rem + 0.9389671362vw,2.5rem);padding:10px clamp(0.625rem,0.5149647887rem + 0.4694835681vw,0.9375rem);color:var(--text-color);font-size:clamp(0.75rem,0.7059859155rem + 0.1877934272vw,0.875rem);font-weight:400;transition:color,background,border var(--transition);display:flex;align-items:center;justify-content:center}.selector:hover{border:0.0625rem solid var(--text-color);color:var(--text-color)}.selector:has(input:checked){color:var(--white);background-color:var(--primery);border-color:var(--white)}.isError{border-color:var(--red);color:var(--red)}.error>.title,.error p{color:var(--red)}.error p{margin-top:5px;margin-left:15px;font-size:0.875rem}.error>.input{border-color:var(--red)}.error>.input>input::placeholder{color:var(--red)}.error>.input>input{color:var(--red)}.error>.input>label>p{color:var(--red)}